.banner {
    height: 380px;
    position: relative;
    overflow: hidden;
    width: 1000px;
    margin: 0 auto;
}

#ad-flash {
    overflow: hidden;
    width: 1000px;
    height: 380px;
}

.focusBox {
    position: relative;
    width: 1000px;
    height: 380px;
    overflow: hidden;
}

.focusBox .pic {
    position: relative;
    z-index: 0;
}

.focusBox .pic li {
    display: none;
}

.focusBox .pic li.on {
    display: block;
}

.focusBox .pic img {
    width: 1000px;
    height: 380px;
    display: block;
}

.focusBox .hd {
    width: 100%;
    position: absolute;
    bottom: 40px;
    text-align: center;
    font-size: 0;
    z-index: 1;
}

.focusBox .hd .on {
    background: #eb5502;
}

.focusBox .hd li {
    margin: 0 5px;
    height: 14px;
    overflow: hidden;
    width: 14px;
    background: #fff;
    cursor: pointer;
    border-radius: 50%;
    display: inline-block;
    zoom: 1;
}

.newbox {
    position: absolute;
    left: 0px;
    bottom: 0px;
    height: 30px;
    width: 100%;
    background: url(../Asset/Img/newbg.png);
    z-index: 99;
}

.newin {
    height: 30px;
    padding-left: 130px;
    width: 670px;
    float: left;
    background: url(../Asset/Img/newtl.png) no-repeat 10px center;
}

.lunleft {
    position: relative;
    overflow: hidden;
    padding-top: 5px;
    height: 25px;
    cursor: pointer;
}

.game {
    margin: 0 -4px;
}

.game li {
    float: left;
    margin: 0 4px;
}

.game li a {
     display: block;
     width: 185px;
     height: 249px;
     background: #212121;
     text-align: center;
     color: #8d8d8d;
     border: 4px solid #212121;
}

.game li a:hover {
    border: 4px solid #EED16A;
}

.da {
    font-size: 18px;
    padding-top: 7px;
}

.xiao {
    font-size: 12px;
    line-height: 18px;
}