body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/bg.5b266001.png) repeat;
  font-size: 12px;
  color: #FFFFFF;
  min-width: 1000px;
  font-family: "\5FAE\8F6F\96C5\9ED1", "Microsoft YaHei", "\5B8B\4F53";
}

img {
  border: none;
}

p {
  margin: auto;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: 400;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clear {
  clear: both;
}

.fl {
  float: left;
  text-align: left;
}

.fr {
  float: right;
  text-align: right;
}

.w1000 {
  width: 1000px;
  height: auto;
  margin: 0 auto;
}

.h5 {
  height: 5px;
}

.h12 {
  height: 12px;
}

.h10 {
  height: 10px;
}

.h15 {
  height: 15px;
}

.red {
  color: #FF9900;
}

.tac {
  text-align: center;
}
.header .top {
    color: #e4cf91;
}

.header .top a {
    color: #e4cf91;
    line-height: 32px;
}

.header .middle {
    height: 86px;
    position: relative;
    background: url(data:image/png;base64,) no-repeat center bottom;
}

.header .middle .logobx {
    position: absolute;
    width: 100%;
    height: 10px;
    z-index: 10;
}

.header .middle .logobx .logo {
    width: 174px;
    height: 178px;
    text-align: center;
    background: url(data:image/png;base64,) no-repeat center top;
    margin: 0 auto;
    padding-top: 8px;
}

.header .middle .time {
    float: left;
    margin-left: 11px;
    padding-left: 20px;
    background: url(data:image/png;base64,) no-repeat left center;
    color: #999999;
}

.header .middle .pai {
    color: #2a2a2a;
}

.header .middle .pai a.pz img {
    position: absolute;
    top: 10px;
    left: 0;
    display: none;
    z-index: 558555;
}

.header .middle .pai .pz {
    color: #f10101;
    padding-left: 22px;
    background: url(data:image/png;base64,) no-repeat left center;
}

.header .middle .pai a {
    position: relative;
    color: #999;
    margin-left: 5px;
}

.header .middle .pai .lang {
    margin-right: 5px;
}

.header .middle .pai .lang img {
    position: relative;
    top: 5px;
}

.header .middle a.regbtn {
    display: block;
    width: 97px;
    height: 30px;
    background: url(/static/media/regbtn.5293470b.png) top center no-repeat;
    color: #4d270b;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    padding-top: 37px;
}

.header .middle .shu {
    float: left;
    margin-right: 5px;
    margin-bottom: 5px;
    border: 1px solid #474747;
    width: 178px;
    height: 28px;
}

.header .middle .shu input {
    border: none;
    height: 28px;
    padding-left: 30px;
    width: 148px;
    color: #999999;
    outline: none;
}

.header .middle .shu .key1 {
    background: url(data:image/png;base64,) no-repeat 10px center;
}

.header .middle .shu .key2 {
    background: url(data:image/png;base64,) no-repeat 10px center;
}

.header .middle .playfor a {
    color: #e4cf91;
    line-height: 28px;
}

.header .nav .fuli {
    float: left;
    position: relative;
    width: 100px;
    height: 56px;
}

.header .nav .fuli .fua {
    display: block;
    text-align: center;
    font-size: 14px;
    line-height: 56px;
    color: #fff;
}

.header .nav .fuli .fua:hover, .header .nav .fuli .fua.active {
    color: #e4cf91;
}

.header .nav .fuli .menu {
    position: absolute;
    top: 50px;
    left: 0px;
    padding-top: 5px;
    display: none;
    width: 96px;
    z-index: 222;
    background: url(data:image/png;base64,) no-repeat center top;
}

.header .nav .fuli .menu .mein {
    background: #000;
    border: 2px solid #e7cd5c;
    padding: 0 3px;
}

.header .nav .fuli .menu .mein li {
    border-bottom: 1px dotted #3d3d3d;
}

.header .nav .fuli .menu .mein li a {
    text-align: center;
    display: block;
    font-size: 12px;
    line-height: 28px;
    color: #fff;
}

.header .nav .fuli .menu .mein li a:hover {
    text-decoration: underline;
}
.cop {
    text-align: center;
    color: #999999;
    line-height: 56px;
    font-size: 12px;
}

.cop a {
    color: #999999;
    padding: 0 10px;
}
.banner {
    height: 380px;
    position: relative;
    overflow: hidden;
    width: 1000px;
    margin: 0 auto;
}

#ad-flash {
    overflow: hidden;
    width: 1000px;
    height: 380px;
}

.focusBox {
    position: relative;
    width: 1000px;
    height: 380px;
    overflow: hidden;
}

.focusBox .pic {
    position: relative;
    z-index: 0;
}

.focusBox .pic li {
    display: none;
}

.focusBox .pic li.on {
    display: block;
}

.focusBox .pic img {
    width: 1000px;
    height: 380px;
    display: block;
}

.focusBox .hd {
    width: 100%;
    position: absolute;
    bottom: 40px;
    text-align: center;
    font-size: 0;
    z-index: 1;
}

.focusBox .hd .on {
    background: #eb5502;
}

.focusBox .hd li {
    margin: 0 5px;
    height: 14px;
    overflow: hidden;
    width: 14px;
    background: #fff;
    cursor: pointer;
    border-radius: 50%;
    display: inline-block;
    zoom: 1;
}

.newbox {
    position: absolute;
    left: 0px;
    bottom: 0px;
    height: 30px;
    width: 100%;
    background: url(data:image/png;base64,);
    z-index: 99;
}

.newin {
    height: 30px;
    padding-left: 130px;
    width: 670px;
    float: left;
    background: url(data:image/png;base64,) no-repeat 10px center;
}

.lunleft {
    position: relative;
    overflow: hidden;
    padding-top: 5px;
    height: 25px;
    cursor: pointer;
}

.game {
    margin: 0 -4px;
}

.game li {
    float: left;
    margin: 0 4px;
}

.game li a {
     display: block;
     width: 185px;
     height: 249px;
     background: #212121;
     text-align: center;
     color: #8d8d8d;
     border: 4px solid #212121;
}

.game li a:hover {
    border: 4px solid #EED16A;
}

.da {
    font-size: 18px;
    padding-top: 7px;
}

.xiao {
    font-size: 12px;
    line-height: 18px;
}

