.header .top {
    color: #e4cf91;
}

.header .top a {
    color: #e4cf91;
    line-height: 32px;
}

.header .middle {
    height: 86px;
    position: relative;
    background: url(../Asset/Img/headerXian.png) no-repeat center bottom;
}

.header .middle .logobx {
    position: absolute;
    width: 100%;
    height: 10px;
    z-index: 10;
}

.header .middle .logobx .logo {
    width: 174px;
    height: 178px;
    text-align: center;
    background: url(../Asset/Img/logoBg.png) no-repeat center top;
    margin: 0 auto;
    padding-top: 8px;
}

.header .middle .time {
    float: left;
    margin-left: 11px;
    padding-left: 20px;
    background: url(../Asset/Img/time.png) no-repeat left center;
    color: #999999;
}

.header .middle .pai {
    color: #2a2a2a;
}

.header .middle .pai a.pz img {
    position: absolute;
    top: 10px;
    left: 0;
    display: none;
    z-index: 558555;
}

.header .middle .pai .pz {
    color: #f10101;
    padding-left: 22px;
    background: url(../Asset/Img/pai.png) no-repeat left center;
}

.header .middle .pai a {
    position: relative;
    color: #999;
    margin-left: 5px;
}

.header .middle .pai .lang {
    margin-right: 5px;
}

.header .middle .pai .lang img {
    position: relative;
    top: 5px;
}

.header .middle a.regbtn {
    display: block;
    width: 97px;
    height: 30px;
    background: url(../Asset/Img/regbtn.png) top center no-repeat;
    color: #4d270b;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    padding-top: 37px;
}

.header .middle .shu {
    float: left;
    margin-right: 5px;
    margin-bottom: 5px;
    border: 1px solid #474747;
    width: 178px;
    height: 28px;
}

.header .middle .shu input {
    border: none;
    height: 28px;
    padding-left: 30px;
    width: 148px;
    color: #999999;
    outline: none;
}

.header .middle .shu .key1 {
    background: url(../Asset/Img/key1.png) no-repeat 10px center;
}

.header .middle .shu .key2 {
    background: url(../Asset/Img/key2.png) no-repeat 10px center;
}

.header .middle .playfor a {
    color: #e4cf91;
    line-height: 28px;
}

.header .nav .fuli {
    float: left;
    position: relative;
    width: 100px;
    height: 56px;
}

.header .nav .fuli .fua {
    display: block;
    text-align: center;
    font-size: 14px;
    line-height: 56px;
    color: #fff;
}

.header .nav .fuli .fua:hover, .header .nav .fuli .fua.active {
    color: #e4cf91;
}

.header .nav .fuli .menu {
    position: absolute;
    top: 50px;
    left: 0px;
    padding-top: 5px;
    display: none;
    width: 96px;
    z-index: 222;
    background: url(../Asset/Img/san.png) no-repeat center top;
}

.header .nav .fuli .menu .mein {
    background: #000;
    border: 2px solid #e7cd5c;
    padding: 0 3px;
}

.header .nav .fuli .menu .mein li {
    border-bottom: 1px dotted #3d3d3d;
}

.header .nav .fuli .menu .mein li a {
    text-align: center;
    display: block;
    font-size: 12px;
    line-height: 28px;
    color: #fff;
}

.header .nav .fuli .menu .mein li a:hover {
    text-decoration: underline;
}